import { AppBar, Divider, Drawer, IconButton, List, ListItem, ListItemIcon } from '@material-ui/core'
import React, { useState } from 'react'
import { makeStyles } from '@material-ui/core'
//rafce
import logo from "../images/logo.svg"
import {Link, animateScroll as scroll} from "react-scroll"
import InfoTwoToneIcon from '@material-ui/icons/Info';
import EmojiObjectsTwoToneIcon from '@material-ui/icons/EmojiObjects';
import BuildTwoToneIcon from '@material-ui/icons/Build';
import ContactMailTwoToneIcon from '@material-ui/icons/ContactMail';
import MenuIcon from '@material-ui/icons/Menu';
import CancelIcon from '@material-ui/icons/Cancel';




const Navbar = () => {
  const links=[
    {
      id:"about",
      text:"About me",
      icon:<InfoTwoToneIcon fontSize='large'/>
    },
    {
      id:"skills",
      text:"Skills",
      icon:<EmojiObjectsTwoToneIcon fontSize='large'/>
    },
    {
      id:"work",
      text:"My Work",
      icon:<BuildTwoToneIcon fontSize='large'/>
    },
    {
      id:"contact",
      text:"Get in touch",
      icon:<ContactMailTwoToneIcon fontSize='large'/>
    },
  ]

  const classes=useStyles();
  const [open, setOpen]=useState(false);
  return (
    <>
    <AppBar position='sticky' className={classes.root}>
        <toolbar className={classes.toolbar}>
          <img src={logo} className={classes.logo} alt="Logo" />
          <List className={classes.menu}>
            {links.map(({ id, text }, index) => (
              <Link
                key={index}
                to={id}
                spy={true}
                activeClass="active"
                smooth={true}
                duration={500}
                offset={-70}>{text}</Link>
            ))}
          </List>
          <IconButton edge="end" className={classes.menuButton} onClick={
          ()=>setOpen(!open)
        }>
            <MenuIcon fontSize='large' />
          </IconButton>
        </toolbar>
      </AppBar>
      <Drawer anchor='right' open={open} onClose={()=>setOpen(false)}>
        <IconButton edge="end" onClick={
          ()=>setOpen(false)
        }>
          <CancelIcon fontSize='large' className={classes.cancelIcon}/>
        </IconButton>
        <Divider/>
        <List className={classes.menu}>
            {links.map(({ id, text ,icon}, index) => (
              <Link
                key={index}
                className={classes.sidebar}
                to={id}
                spy={true}
                activeClass="active"
                smooth={true}
                duration={500}
                offset={-70}>
                  <ListItem component="h5">
                    <span>
                      <ListItemIcon>
                        {icon}
                      </ListItemIcon>
                    </span>
                    {text}
                  </ListItem>
                </Link>
            ))}
          </List>
      </Drawer>
      </>
  )
}

const useStyles= makeStyles((theme)=>({
  root:{
    backgroundColor:"#fafafa",
    top:0,
    left:0,
    right:0,
    zIndex:999,
  },
  logo:{
    height:"1.5rem",
    objectFit:"contain",
    "&:hover":{
      cursor: "pointer"
    }
  },
  toolbar:{
    display:"flex",
    justifyContent:"flex-start",
    alignItems:"center",
  },
  menu:{
    [theme.breakpoints.down("sm")]:{
      display:"none",
    },
    "& a":{
      color:"#333",
      fontSize:"1.4rem",
      fontWeigth:"bold",
      marginLeft:theme.spacing(3)
    },
    "& a:hover ":{
      cursor:"pointer",
      color:"tomato",
      borderBottom:"3px solid tomato",
    }
  },
  menuButton:{
    display:"none",
    [theme.breakpoints.down("sm")]:{
      display:"block",
      color:"tomato",
      position:"absolute",
      top:0,
      right:10,
      height:"64"
    },
  },
  sidebar:{
    width:"40vw",
    [theme.breakpoints.down("sm")]:{
      width:"60vw",
    },
    "& h5":{
      margin:theme.spacing(10,0,0,4),
      fontSize:"1.4rem",
      color:"#333",
      fontWeigth:"bold",
    },
    "& h5:hover":{
      color:"tomato",
      cursor:"pointer",
    }
  },
  cancelIcon:{
    color:"tomato",
    position:"absolute",
    top:0,
    right:10,
  }
}))

export default Navbar